const { DocumentSheetV2, HandlebarsApplicationMixin } = foundry.applications.api;

// Add Vision Sharing sheet to ActorDirectory context options
function addVisionSharingContextMenu(html, entries) {
  entries.push({
    name: "PF1.Application.VisionSharing.Label",
    icon: '<i class="fa-solid fa-eye"></i>',
    condition: () => game.user.isGM,
    callback: (li) => {
      if (li instanceof jQuery) li = li[0]; // v12/v13 cross-compatibility
      const actorId = li.dataset.entryId ?? li.dataset.documentId; // documentId is used by v12 and older
      game.actors.get(actorId).visionSharingSheet.render(true, {
        position: {
          // Positioning copied from Foundry's ownership dialog
          top: Math.min(li.offsetTop, window.innerHeight - 370),
          left: window.innerWidth - 720,
        },
      });
    },
  });
}

Hooks.on("getActorDirectoryEntryContext", addVisionSharingContextMenu); // v12
Hooks.on("getActorContextOptions", addVisionSharingContextMenu); // v13

export class VisionSharingSheet extends HandlebarsApplicationMixin(DocumentSheetV2) {
  static DEFAULT_OPTIONS = {
    tag: "form",
    classes: ["pf1-v2", "vision-sharing", "standard-form"],
    window: {
      minimizable: false,
    },
    position: {
      width: 400,
      height: "auto",
    },
    form: {
      handler: this._onSubmit,
      closeOnSubmit: true,
      submitOnChange: false,
    },
    actions: {
      reset: this._onReset,
    },
    sheetConfig: false,
  };

  static PARTS = {
    form: {
      template: "systems/pf1/templates/apps/vision-sharing.hbs",
    },
    footer: {
      template: "templates/generic/form-footer.hbs",
    },
  };

  get title() {
    let title = game.i18n.localize("PF1.Application.VisionSharing.Label") + `: ${this.actor.name}`;
    if (this.actor.token) title += "[" + game.i18n.localize("Token") + "]";
    return title;
  }

  /**
   * @internal
   * @param {*} v
   * @returns {boolean|null}
   */
  _getUniformValue(v) {
    if (v === "true" || v === "yes" || v === true) return true;
    if (v === "false" || v === "no" || v === false) return false;
    return null;
  }

  async _prepareContext() {
    const config = this._config ?? this.actor.getFlag("pf1", "visionSharing");
    this._config = null;

    const context = {
      fields: {
        default: new foundry.data.fields.StringField({
          choices: { true: "PF1.Yes", false: "PF1.No" },
          label: "PF1.Default",
        }),
        toggle: new foundry.data.fields.StringField({
          choices: { true: "PF1.Yes", false: "PF1.No" },
        }),
      },
      default: this._getUniformValue(config?.default) ?? false,
      users: game.users.players.reduce((rv, user) => {
        rv[user.id] = {
          user,
          level: String(this._getUniformValue(config?.users?.[user.id]?.level ?? config?.users?.[user.id] ?? null)),
        };
        return rv;
      }, {}),
    };

    // Footer buttons
    context.buttons = [
      { type: "reset", label: "PF1.Reset", icon: "fa-solid fa-arrow-rotate-left", action: "reset" },
      { type: "submit", label: "PF1.Save", icon: "fa-solid fa-floppy-disk" },
    ];

    return context;
  }

  /** @type {Actor} */
  get actor() {
    return this.document;
  }

  /**
   * @this {VisionSharingSheet}
   * @param {Event} event
   * @param {HTMLFormElement} form
   * @param {FormDataExtended} formData
   */
  static async _onSubmit(event, form, formData) {
    formData = foundry.utils.expandObject(formData.object);

    formData.users ??= {};

    // Clear users that are using default
    for (const [userId, value] of Object.entries(formData.users)) {
      const enabled = this._getUniformValue(value);
      if (enabled) formData.users[userId] = true;
      else {
        formData.users[`-=${userId}`] = null;
        delete formData.users[userId];
      }
    }

    formData.default = this._getUniformValue(formData.default);

    // Remove old users that no longer exist
    const oldUsers = this.actor.getFlag("pf1", "visionSharing")?.users;
    if (oldUsers) {
      for (const userId of Object.keys(oldUsers)) {
        if (!game.users.get(userId)) {
          formData.users[`-=${userId}`] = null;
        }
      }
    }

    // Delete users object entirely if it doesn't exist
    const userUpdateKeys = Object.keys(formData.users);
    if (userUpdateKeys.length === 0 || userUpdateKeys.every((k) => k.startsWith("-="))) {
      delete formData.users;
      formData["-=users"] = null;
    }

    // Save if either default is true or have any user overrides
    if (formData.default || formData.users) {
      await this.actor.setFlag("pf1", "visionSharing", formData);
      console.debug("PF1 | Vision Sharing saved for", this.actor.name);
    }
    // Otherwise clear the saved data
    else {
      await this.actor.unsetFlag("pf1", "visionSharing");
      console.debug("PF1 | Vision Sharing reset for", this.actor.name);
    }
  }

  /**
   * @this {VisionSharingSheet}
   * @param {PointerEvent} _event - Triggering event
   * @param {HTMLElement} _html - Button
   */
  static async _onReset(_event, _html) {
    // Record config replacement
    this._config = {
      default: false,
      users: {},
    };

    this.render();
  }
}
