import { NoAutocomplete } from "./mixins/no-autocomplete.mjs";
const { ApplicationV2, HandlebarsApplicationMixin } = foundry.applications.api;

/**
 * Apply Damage Prompt
 *
 * Handles damage application UX when adjustment is desired.
 *
 * Calls {@link ActorPF#applyDamage} when finally committing changes.
 *
 * Called primarily from {@link ActorPF.applyDamage}
 */
export class ApplyDamage extends HandlebarsApplicationMixin(NoAutocomplete(ApplicationV2)) {
  /**
   * Base damage/healing value
   *
   * @type {number}
   */
  value = 0;

  /**
   * Arbitrary manual bonus
   *
   * @type {number}
   */
  bonus;

  /**
   * Type of the custom bonus.
   *
   * @type {string}
   */
  bonusType = "untyped";
  /**
   * Is this healing instead of damage?
   *
   * @type {boolean}
   */
  isHealing;

  /**
   * Is this to be applied directly to wounds with Wounds & Vigor optional rule?
   *
   * @type {boolean}
   */
  asWounds;

  /**
   * Critical multiplier.
   *
   * @type {number}
   */
  critMult;

  /**
   * Nonlethal
   *
   * @type {boolean}
   */
  asNonlethal;

  /**
   * General reduction applied to all
   *
   * @type {number}
   */
  reduction;

  /**
   * Targets
   *
   *  @type {Collection<ApplyDamageTarget>}
   */
  targets;

  /**
   * Damage instances
   *
   * @type {Array<pf1.models.action.DamagePartModel>}
   */
  instances = [];

  /**
   * Does the damage have enhancement bonus or otherwise count as magic?
   *
   * @type {boolean}
   */
  isMagic = false;

  /**
   * Damage materials
   *
   * @type {Set<string>}
   */
  materials = new Set();

  /**
   * All resistances that exist.
   *
   * @type {Set<string>}
   */
  resistanceIds = new Set();

  /**
   * All damage types that exist
   *
   * @type {Set<string>}
   */
  damageIds = new Set();

  /** @type {Function} */
  resolve;

  /**
   * Does this count as real adamantine?
   *
   * @type {boolean}
   */
  adamantine = false;

  /**
   * Allow bonus value
   *
   * @experimental
   */
  static ALLOW_BONUS = true;

  /**
   * Adamantine penetration of hardness
   *
   * It however only ignores hardness up to 20, but if it's higher, hardness applies in full.
   *
   * @todo
   * - Move this datapoint elsewhere
   * @experimental
   */
  static ADAMANTINE_THRESHOLD = /** @type {const} */ (20);
  static ADAMANTINE_ID = /** @type {const} */ ("adamantine");

  constructor(options) {
    const isHealing = options.value < 0;
    if (isHealing) options.value = -options.value;

    const { resolve } = options;

    super(options);

    this.extraOptions = {
      action: options.action,
      attackIndex: options.attackIndex,
      reference: options.reference,
      event: options.event,
      message: options.message,
      element: options.element,
      interactive: true,
      dialog: true,
    };

    this.damageOptions = {
      asNonlethal: options.asNonlethal ?? false,
      asWounds: options.asWounds ?? false,
      critMult: options.critMult ?? 0,
      dualHeal: options.dualHeal ?? game.settings.get("pf1", "dualHeal"),
    };

    // TODO: Preserve arbitrary options

    Object.defineProperties(this, {
      resolve: {
        value: resolve,
        writable: true,
        enumerable: false,
      },
      isHealing: {
        value: isHealing,
        writable: false,
      },
      asWounds: {
        value: options.asWounds ?? false,
      },
      asNonlethal: {
        value: options.asNonlethal ?? false,
      },
      critMult: {
        value: options.critMult || 0,
      },
      value: {
        value: options.value,
        writable: true,
      },
      reduction: {
        value: options.reduction,
        writable: true,
      },
      woundsAndVigor: {
        value: false,
        writable: true,
      },
    });

    this._prepareInstances();
    this._evaluateAttack();
    this._prepareTargets();
  }

  static DEFAULT_OPTIONS = {
    tag: "form",
    classes: ["pf1-v2", "apply-damage", "standard-form"],
    window: {
      resizable: true,
      minimizable: false,
    },
    position: {
      width: 460,
      height: "auto",
    },
    actions: {
      apply: this._onApply,
    },
  };

  get title() {
    return this.isHealing ? game.i18n.localize("PF1.ApplyHealing") : game.i18n.localize("PF1.ApplyDamage");
  }

  static PARTS = {
    form: {
      template: "systems/pf1/templates/apps/apply-damage-dialog.hbs",
    },
    footer: {
      template: "templates/generic/form-footer.hbs",
    },
  };

  _preparePartContext(partId, context, _options) {
    switch (partId) {
      case "footer": {
        context.buttons = [
          {
            action: "apply",
            type: "submit",
            label: game.i18n.localize("PF1.Apply"),
            icon: "fa-solid fa-heart-pulse",
            default: true,
          },
        ];
        break;
      }
      case "form": {
        this._prepareFormContext(context);
        break;
      }
    }

    return context;
  }

  _prepareFormContext(context) {
    context.value = this.value;
    context.bonus = this.bonus;
    context.bonusType = this.bonusType;
    context.isHealing = this.isHealing;
    context.reduction = this.reduction;
    context.targets = this.targets;
    context.instances = this.instances;
    context.hasPhysical = this.hasPhysical;
    context.hasEnergy = this.hasEnergy;
    context.options = this.damageOptions;
    context.useWoundsAndVigor = this.woundsAndVigor;
    context.multiTarget = context.targets.size > 1;

    context.damageTypes = pf1.registry.damageTypes.getLabels({ sort: false });
    for (const [key, dt] of pf1.registry.damageTypes.entries()) {
      if (dt.isUtility) delete context.damageTypes[key];
    }

    context.tags = [];

    this._addContextTags(context.tags);
  }

  /**
   * @experimental
   * @param {Array<object>} flags
   */
  _addContextTags(flags) {
    if (this.isHealing) return;

    const ids = new Set();

    // Add magic
    if (this.isMagic && this.resistanceIds.has("magic")) {
      const magic = pf1.registry.materials.get("magic");
      if (magic) flags.push({ id: "magic", label: magic.shortName || magic.name, data: magic });
      ids.add("magic");
    }

    // Add materials
    for (const materialId of this.materials) {
      if (this.resistanceIds.has(materialId) && !ids.has(materialId)) {
        ids.add(materialId);
        const material = pf1.registry.materials.get(materialId);

        if (material) flags.push({ id: materialId, label: material.shortName || material.name, data: material });
        else {
          const align = pf1.config.damageResistances[materialId];
          if (align) flags.push({ id: materialId, label: align });
        }
      }
    }

    // Special case for adamantine
    // Display if attack has adamantine and any of the targets have hardness
    const ae = this.constructor.ADAMANTINE_THRESHOLD || 0;
    const adamantineId = this.constructor.ADAMANTINE_ID;
    if (ae > 0 && this.adamantine && !ids.has(adamantineId)) {
      if (this.targets.some((t) => t.hardness.available)) {
        ids.add(adamantineId);
        const material = pf1.registry.materials.get(adamantineId);
        if (material) flags.push({ id: adamantineId, label: material.shortName || material.name, data: material });
      }
    }
  }

  /**
   * @override
   * @param {object} options
   * @param {Event} event
   */
  _onChangeForm(options, event) {
    const formData = foundry.utils.expandObject(new FormDataExtended(this.element).object);

    const name = event.target.name;
    const parts = name.split(".");
    if (parts[0] === "targets") {
      const idx = parts[1];
      const data = formData.targets[idx];
      this._addTargetData(data.uuid, data);
    }

    switch (name) {
      case "value":
        this.value = formData.value;
        break;
      case "bonus":
        this.bonus = formData.bonus;
        break;
      case "bonusType":
        this.bonusType = formData.bonusType || "untyped";
        break;
      case "reduction":
        this.reduction = formData.reduction;
        break;
    }

    for (const target of this.targets) this._refreshTarget(target.uuid);

    // Merge options
    foundry.utils.mergeObject(this.damageOptions, formData.options);

    // TODO: Somehow preserve any module added data

    // Re-render if no action was triggered
    if (!event.target.dataset?.action) this.render({ parts: ["form"] });
  }

  /** @override */
  _onClose() {
    super._onClose();

    this.resolve?.(null);
  }

  /**
   * Prepare damage/healing instances.
   *
   * Merge damage instances of same type.
   */
  _prepareInstances() {
    const instances = this.options.instances ?? [];

    // Add a fake damage instance if no instances are present.
    // Mostly to make it clear the damage is being treated as untyped.
    if (instances.length === 0 && this.options.value > 0) {
      const part = new pf1.models.action.DamagePartModel({ types: ["untyped"] });
      part.value = this.options.value || 0;
      instances.push(part);
    }

    for (let i = 1; i < instances.length; i++) {
      const cur = instances[i];
      if (cur.value == 0) continue;

      // Merge with previous if one exists
      for (let y = 0; y < i; y++) {
        const prev = instances[y];
        if (prev.value == 0) continue;
        if (cur.types.equals(prev.types)) {
          prev.value += cur.value;
          cur.value = 0;
          break;
        }
      }
    }

    Object.defineProperty(this, "instances", {
      value: instances.filter((i) => i.value != 0),
      writable: false,
      enumerable: false,
    });

    // Record all known damage type IDs
    for (const instance of this.instances) {
      instance.typeIds = new Set(instance.types ?? []);
      for (const type of instance.typeIds) this.damageIds.add(type);
    }
  }

  /**
   * Evaluate attack and attacker info
   *
   * Determines attack materials, enhancement bonus, natural attack DR/magic penetration, etc.
   *
   * @todo
   * - Some of this data should be cached in the chat messages and not rely on the action's current state
   */
  _evaluateAttack() {
    /** @type {pf1.components.ItemAction} */
    const action = this.options.action;
    if (!action) return;

    /** @type {ActorPF} */
    const actor = action.actor; // Attacker
    const item = action.item;

    const enhancement = action.enhancementBonus || 0;
    const hasEnhancement = enhancement > 0;
    const isNatural = action.item?.subType === "natural";

    // Attacker having DR/magic with naturals penetrates the same
    let hasDRMagic = false;
    if (actor && isNatural) {
      const resistances = [];
      const drEntries = actor.system.traits?.dr?.value ?? [];
      for (const entry of drEntries) resistances.push(actor._parseResistanceEntry(entry, "dr"));
      hasDRMagic = resistances.some((r) => r.type0?.id === "magic" || r.type1?.id === "magic");
    }

    // Handle materials
    const materials = this.materials;
    // Base material
    const material = pf1.registry.materials.get(action?.normalMaterial);
    if (material) {
      materials.add(material.id);
      if (material.treatedAs) materials.add(material.treatedAs);
    }

    // Addon materials
    for (const addon of action.addonMaterial) {
      materials.add(addon);
      const am = pf1.registry.materials.get(addon);
      if (am?.treatedAs) materials.add(am.treatedAs);
    }

    // Add special marker for adamantine
    // Enhancement bonus granted adamantine penetration does not give full effects
    if (materials.has(this.constructor.ADAMANTINE_ID)) this.adamantine = true;

    // Material penetrations granted by enhancement bonus
    if (enhancement > 0) {
      for (let i = 0; i <= enhancement; i++) {
        const em = pf1.config.enhancementMaterialPenetration[i] ?? [];
        for (const materialId of em) materials.add(materialId);
      }
    }

    // Alignments
    if (item) {
      for (const alignmentId of Object.keys(pf1.config.damageResistances)) {
        const enabled = action.alignments?.[alignmentId] ?? item.alignments?.[alignmentId] ?? false;
        if (enabled) materials.add(alignmentId);
      }
    }

    // Record it all
    this.isMagic = hasEnhancement || (isNatural && hasDRMagic);
  }

  _prepareTargets() {
    /** @type {Array<pf1.models.action.DamagePartModel>} */
    const instances = this.instances ?? [];
    for (const instance of instances) {
      instance.label = pf1.utils.i18n.join(instance.names, "u", true);
    }

    const unknownTypes =
      instances.length === 0 ||
      instances.some((i) => i.standard.some((t) => !["physical", "energy"].includes(t.category)));

    Object.defineProperties(this, {
      hasPhysical: {
        value: this.isHealing
          ? false
          : unknownTypes
            ? true
            : instances.some((i) => i.standard.some((t) => t.isPhysical)),
        enumerable: false,
        writable: false,
      },
      hasEnergy: {
        value: this.isHealing ? false : unknownTypes ? true : instances.some((i) => i.standard.some((t) => t.isEnergy)),
        enumerable: false,
        writable: false,
      },
    });

    Object.defineProperty(this, "targets", {
      value: new Collection(),
      writable: false,
      enumerable: false,
    });

    /** @type {pf1.applications.settings.HealthConfigModel} */
    const hpconfig = game.settings.get("pf1", "healthConfig");

    for (const actor of this.options.targets) {
      const hardness = actor.system.traits?.hardness || 0;
      const hasHardness = this.isHealing ? false : hardness > 0;

      const target = {
        uuid: actor.uuid,
        actor,
        name: actor.token?.name || actor.name,
        value: this.value,
        isToken: !!actor.token,
        ratio: 1,
        dr: this.hasPhysical ? this.getResistances(actor, "dr") : [],
        get visibleDR() {
          return this.dr.some((r) => r.visible);
        },
        eres: this.hasEnergy ? this.getResistances(actor, "eres") : [],
        get visibleER() {
          return this.eres.some((r) => r.visible);
        },
        hardness: {
          active: false,
          available: hasHardness,
          value: hardness,
          disabled: true,
        },
        reduction: {
          total: 0,
          manual: undefined, // Manually entered
        },
        di: this.isHealing ? [] : this._getRelevantDamageTypes(actor.system.traits?.di, true),
        dv: this.isHealing ? [] : this._getRelevantDamageTypes(actor.system.traits?.dv, false),
        selected: true,
      };

      if (hasHardness) {
        // Adamantine effect is applied here to see if it's even relevant
        let effectiveHardness = hardness;
        if (this._needsHardnessAdjust(target, hardness)) {
          effectiveHardness = this._applyHardnessAdjust(target, hardness);
        }
        target.hardness.active = effectiveHardness > 0;
        target.hardness.disabled = effectiveHardness <= 0;
        target.hardness.effective = effectiveHardness;
      }

      target.haveDIV = target.di.length || target.dv.length;
      target.haveDER = target.dr.length || target.eres.length || target.hardness.effective;

      this.targets.set(target.uuid, target);

      this._refreshTarget(target.uuid);

      const cfg = hpconfig.getActorConfig(actor);
      this.woundsAndVigor ||= cfg.rules.useWoundsAndVigor ?? false;
    }
  }

  _getRelevantDamageTypes(trait, enable = false) {
    const rv = [];
    if (!trait) return rv;

    for (const typeId of trait.standard) {
      if (!this.damageIds.has(typeId)) continue; // Skip instances that don't occur

      const type = pf1.registry.damageTypes.get(typeId);
      rv.push({ label: type?.name || typeId, id: typeId, active: enable, disabled: !enable });
    }

    for (const custom of trait.custom) rv.push({ label: custom });

    return rv;
  }

  /**
   *
   * @param {ActorPF} actor
   * @param {"dr"|"eres"} resistanceType
   * @returns {object[]} - Array of results from {@link ActorPF._parseResistance()}
   */
  getResistances(actor, resistanceType = "dr") {
    const res = actor.system.traits?.[resistanceType];
    if (!res) return [];

    const resistances = [];

    if (res.custom) {
      for (const entry of res.custom.split(pf1.config.re.traitSeparator)) {
        const re = actor._parseResistanceEntry(entry, resistanceType);
        resistances.push(re);
      }
    }
    if (res.value?.length) {
      for (const entry of res.value) {
        const re = actor._parseResistanceEntry(entry, resistanceType);
        resistances.push(re);
      }
    }

    const allDamageTypes = new Set(this.instances.map((i) => [...i.types]).flat());

    // Types common to all instances
    const commonTypes = new Set();
    // Is magical attack?
    if (this.isMagic) commonTypes.add("magic");
    // Has relevant material?
    for (const material of this.materials) commonTypes.add(material);

    for (const type of commonTypes) allDamageTypes.add(type);

    // Add in data used by this dialog
    const enrichResistance = (res) => {
      res.value = res.amount;
      res.active = false; // Toggled
      res.types = [res.type0?.id, res.type1?.id].filter((t) => t);
      res.typeIds = new Set(res.types);
      res.visible = true; // Shown
      //res.enabled = true; // UNUSED; Available for toggling
      res.hasGeneric = res.isDR && (res.typeIds.size === 0 || res.typeIds.has(""));

      for (const type of res.typeIds) this.resistanceIds.add(type);

      // Set generic resistances to be active by default
      if (res.hasGeneric) {
        res.active = true;
      }
      // Make custom resistance always available but not enabled by default
      else if (res.custom) {
        // NOP, don't disable but don't activate either
      }
      // DR is exclusive
      // TODO: Make this mess cleaner and more clear in what it does
      else if (res.isDR) {
        // Operator: true -> OR; false -> AND
        if (res.operator) {
          // OR; if any of the resistance types are in the damage types, the resistance is overcome
          res.active = !res.typeIds.some((t) => allDamageTypes.has(t));
          res.active ||= this.instances.some((i) => !res.typeIds.intersects(i.typeIds.union(commonTypes)));
        } else {
          // AND; if all of the resistance types are in the damage types, the resistance is overcome
          res.active = !res.typeIds.every((t) => allDamageTypes.has(t));
          res.active ||= !this.instances.every((i) => !res.typeIds.intersects(i.typeIds.union(commonTypes)));
        }

        // Consider resistances that weren't activated for disabling entirely
        if (!res.active) {
          // If all types are listed in general types, disable choice
          res.disabled = res.typeIds.every((t) => commonTypes.has(t));
          // Also if the types are present in every instance
          res.disabled ||= this.instances.every((i) => res.typeIds.intersects(i.typeIds));
        }
      }
      // ER is inclusive
      else if (res.isER) {
        res.active = res.typeIds.some((t) => allDamageTypes.has(t));

        // ER not activated by default is not relevant to display
        if (!res.active) res.disabled = true;
      }
    };

    for (const res of resistances) enrichResistance(res);

    return resistances;
  }

  /**
   * Does the reduction match the damage?
   *
   * @param {ApplyDamageTarget} target - Target
   * @param {object} instance - Damage instance
   * @param {object} reduction - Reduction info
   * @param {boolean} dr - Is this DR or ER?
   * @returns {boolean}
   */
  _isReducedBy(target, instance, reduction, dr) {
    let match = false;

    // Base type mismatch
    if ((dr && !instance.isPhysical) || (!dr && !instance.isEnergy)) return false;

    // Generic and custom reduction always applies
    if (reduction.hasGeneric || reduction.custom) return true;

    const types = new Set(instance.typeIds); // Copy
    if (dr) {
      if (this.isMagic) types.add("magic");
      for (const materialId of this.materials) types.add(materialId);
    }

    // Check actual types
    // OR @deprecated
    if (reduction.operator) {
      match = reduction.typeIds.some((t) => types.has(t));
    }
    // AND
    else {
      // AND; if all of the resistance types are in the damage types, the resistance is overcome
      match = reduction.typeIds.every((t) => types.has(t));
    }

    // DR if not matching reduces (declares penetration)
    // ER if matching reduces (declares negation)
    return dr ? !match : match;
  }

  /**
   * Apply damage reduction
   *
   * Alters `instance.total` and `reduction.available` as necessary.
   *
   * @param {ApplyDamageTarget} target - Target
   * @param {object} instance - Damage instance
   * @param {object} reduction - Reduction
   * @param {boolean} dr - Is this DR or ER?
   * @returns {number} - How much did this reduce?
   */
  _applyReduction(target, instance, reduction, dr) {
    if (instance.total <= 0) return 0;
    if (reduction.available <= 0) return 0;

    // Cache original availability
    const available = reduction.available;

    // Reduce available reduction
    reduction.available -= instance.total;

    // If available reduction is not enough to consume all damage, overflow remains as actual damage
    let overflow = 0;
    if (reduction.available < 0) {
      overflow = Math.abs(reduction.available);
      reduction.available = 0; // Normalize to zero, meaningless detail as this makes no difference
    }
    // No else, initial zero is correct if the damage was wholly consumed by reduction

    instance.total = overflow;

    // Return actual reduction
    return available - reduction.available;
  }

  _processImmunities(target) {
    const immunities = target.di;

    let immunized = 0;

    for (const immunity of immunities) {
      if (!immunity.active) continue;
      for (const instance of target.instances) {
        // TODO: Mixed damage
        instance.immune = instance.typeIds.every((t) => t === immunity.id);
        if (instance.immune) {
          immunized += instance.total;
          instance.total = 0;
        }
      }
    }

    return immunized;
  }

  /**
   * Is hardness needing to be modified by any reason?
   *
   * @param {ApplyDamageTarget} target - Target
   * @param {number} hardness - Hardness value
   * @returns {boolean}
   */
  _needsHardnessAdjust(target, hardness) {
    if (hardness <= 0) return false;
    if (!this.adamantine) return false;

    // Adamantine has effect only on hardness of 20 or less
    return hardness <= (this.constructor.ADAMANTINE_THRESHOLD || 0);
  }

  /**
   * Apply hardness modification
   *
   * @param {ApplyDamageTarget} target - Target
   * @param {number} hardness - Hardness
   * @returns {number} - New hardness
   */
  _applyHardnessAdjust(target, hardness) {
    // Adamantine ignores hardness of 20 or less. It does not flat reduce hardness like you would expect
    if (hardness <= (this.constructor.ADAMANTINE_THRESHOLD || 0)) return 0;

    return hardness;
  }

  /**
   * Process reductions against available instances.
   *
   * @param target
   * @param {object[]} instances
   * @param {object[]} reductions
   * @param {boolean} dr - Are these DR or ER?
   * @returns {number} - Total reduction
   */
  _processReductions(target, instances, reductions, dr) {
    let total = 0;

    for (const r of reductions) {
      if (!r.active) continue;
      if (r.value <= 0) continue;
      r.available = r.value;

      for (const instance of instances) {
        if (instance.total <= 0) continue;

        if (!this._isReducedBy(target, instance, r, dr)) continue;

        total += this._applyReduction(target, instance, r, dr);

        // Stop loop early if reduction ran out of juice
        if (r.available <= 0) break;
      }
    }

    return total;
  }

  _addTargetData(targetId, data) {
    const target = this.targets.get(targetId);

    const { dr, eres, ratio, di, reduction, hardness } = data;

    foundry.utils.mergeObject(target, { ratio, reduction: { manual: reduction }, hardness: { active: hardness } });

    if (dr) {
      for (let i = 0; i < target.dr.length; i++) dr[i] ??= { active: false };
      Object.values(dr).forEach((rdata, idx) => foundry.utils.mergeObject(target.dr[idx], rdata));
    }

    if (eres) {
      for (let i = 0; i < target.eres.length; i++) eres[i] ??= { active: false };
      Object.values(eres).forEach((rdata, idx) => foundry.utils.mergeObject(target.eres[idx], rdata));
    }

    if (di) {
      for (let i = 0; i < target.di.length; i++) di[i] ??= { active: false };
      Object.values(di).forEach((rdata, idx) => foundry.utils.mergeObject(target.di[idx], rdata));
    }
  }

  /**
   * Update target data based on form data and recalculate values.
   *
   * @internal
   * @param {string} targetId
   */
  _refreshTarget(targetId) {
    const target = this.targets.get(targetId);

    // Update value
    // TODO: Account for this.value being altered (shunt to untyped bonus or reduction)
    target.value = Math.floor(this.value * (target.ratio ?? 0));
    target.instances = pf1.utils.deepClone(this.instances);

    // No instances? Push raw value as generic instance
    if (target.instances.length == 0) {
      target.instances.push({
        value: this.value || 0,
        types: ["untyped"],
      });
    }

    // Add bonus value
    target.instances.push({
      value: this.bonus || 0,
      types: [this.bonusType],
    });

    // Initialize instances to this actor
    for (const instance of target.instances) {
      instance.total = Math.floor(instance.value * (target.ratio ?? 0));

      instance.typeIds = new Set(instance.types);

      const types = instance.typeIds.map(
        (t) => pf1.registry.damageTypes.get(t) || { id: t, isPhysical: false, isEnergy: false }
      );

      instance.isPhysical = types.some((t) => t.isPhysical);
      instance.isEnergy = types.some((t) => t.isEnergy);
      instance.isMixed = instance.isPhysical && instance.isEnergy; // Both
      instance.isUntyped = !instance.isPhysical && !instance.isEnergy; // Neither
    }

    // Reduction choices
    target.reduction.total = 0;

    // Process immunities
    const immunized = this._processImmunities(target);
    target.reduction.immunized = immunized;
    target.reduction.total += immunized;

    // Apply physical reduction
    const physicalReduction = this._processReductions(target, target.instances, target.dr, true);
    target.reduction.physical = physicalReduction;
    target.reduction.total += physicalReduction;

    // Apply energy reduction
    const energyReduction = this._processReductions(target, target.instances, target.eres, false);
    target.reduction.energy = energyReduction;
    target.reduction.total += energyReduction;

    // Hardness
    if (target.hardness.active) {
      const hardness = target.hardness;
      hardness.available = hardness.total = hardness.effective || 0;
      hardness.reduced = 0;

      for (const instance of target.instances) {
        if (instance.isUntyped) continue;
        hardness.reduced += this._applyReduction(target, instance, hardness);
        if (hardness.available <= 0) break;
      }

      target.reduction.hardness = hardness.reduced;
      target.reduction.total += hardness.reduced;
    }

    // Per-target and general reduction
    const targetReduction = target.reduction.manual || 0;
    const generalReduction = this.reduction || 0;
    if (targetReduction || generalReduction) {
      const tr = { available: targetReduction, total: targetReduction, reduced: 0 };
      const gr = { available: generalReduction, total: generalReduction, reduced: 0 };

      for (const instance of target.instances) {
        if (instance.isUntyped) continue;
        tr.reduced += this._applyReduction(target, instance, tr);
        gr.reduced += this._applyReduction(target, instance, gr);
      }

      target.reduction.general = gr.reduced;
      target.reduction.individual = tr.reduced;
      target.reduction.total += tr.reduced + gr.reduced;
    } else {
      target.reduction.general = 0;
      target.reduction.individual = 0;
    }

    // Calculate total remaining health adjust
    target.value = 0;
    for (const instance of target.instances) {
      target.value += instance.total;
    }

    target.total = target.value;
  }

  /**
   * Apply health change
   *
   * @param {Event} event
   * @param {HTMLElement} target
   * @this {ApplyDamage}
   */
  static async _onApply(event, target) {
    event.preventDefault();

    const resolve = this.resolve;
    this.resolve = null; // Prevent _onClose() calling resolve
    const config = {
      value: this.options.value,
      reduction: this.reduction || 0,
      isHealing: this.isHealing,
      asWounds: this.asWounds,
      asNonlethal: this.asNonlethal,
      instances: this.instances,
      targets: this.targets,
    };

    // This causes clicks to go through the dialog
    this.element.inert = true;

    // Applied value with general damage reduction accounted for
    let value = Math.max(0, this.value);
    if (this.isHealing) value = -value; // Flip healing

    // Add bonus to value
    value += this.bonus || 0;

    const updated = [];
    try {
      const promises = [];

      for (const target of this.targets) {
        const applyDamageOpts = this._getTargetDamageOptions(target);
        console.debug(
          "PF1 | Applying",
          value,
          "damage with",
          applyDamageOpts.reduction,
          "reduction to",
          target.actor.name,
          `[${target.actor.uuid}]`
        );
        const p = target.actor.applyDamage(value, applyDamageOpts);

        p.then((r) => (r ? updated.push(target.actor) : null));

        promises.push(p);
      }

      const targetCount = await Promise.all(promises).then((res) => res.filter((r) => !!r).length);

      value = Math.abs(value);

      if (this.isHealing)
        ui.notifications.info(game.i18n.format("PF1.Application.Damage.ApplyHeal", { targets: targetCount }));
      else ui.notifications.info(game.i18n.format("PF1.Application.Damage.ApplyDamage", { targets: targetCount }));
    } catch (err) {
      ui.notifications.error(err.message, { console: false });
      throw err;
    } finally {
      this.close();

      config.updated = updated;
      resolve(config);
    }
  }

  /**
   * Get per-target options to pass to {@link ActorPF.prototype.applyDamage ActorPF#applyDamage()}
   *
   * @param {ApplyDamageTarget} target - Target
   * @returns {object} - Options
   */
  _getTargetDamageOptions(target) {
    const options = {
      ...this.damageOptions, // Spread damage options in, allowing modules to extend the options and slip in extra data
      ...this.extraOptions, // Additional options that might otherwise be lost
      reduction: target.reduction.total || 0,
      ratio: target.ratio ?? 1,
      instances: target.instances.map((i) => {
        const ins = new pf1.models.action.DamagePartModel(pf1.utils.deepClone(i));
        for (const k of Object.keys(i)) {
          ins[k] = i[k];
        }
        return ins;
      }),
    };

    Hooks.callAll("pf1ApplyDamageTargetOptions", this, target, options);

    return options;
  }

  /**
   * @param {object} options - Options passed to apply damage
   * @param {object} [renderOptions] - Render options passed to the application.
   * @returns {Promise<null|object>} - Null if cancelled, object describing applied settings.
   */
  static async wait(options, renderOptions = {}) {
    return new Promise((resolve) => {
      options.resolve = resolve;
      const app = new this(options);
      app.render(true, renderOptions);
    });
  }
}
