import { BOOLEAN_OPERATOR, CheckboxFilter } from "./checkbox.mjs";

export class PackFilter extends CheckboxFilter {
  static label = "PF1.Compendium";
  static indexField = "__pack";

  /** @override */
  prepareChoices() {
    const entries = this.compendiumBrowser?.entries.contents;
    const usedPacks = this.compendiumBrowser?.packs
      ?.filter((pack) => entries.some((entry) => entry.__pack === pack.collection))
      .map((pack) => {
        const label = pack.metadata.label;
        return { key: pack.collection, label: game.i18n.has(label) ? game.i18n.localize(label) : label };
      });
    const orderedPacks = pf1.utils.naturalSort(usedPacks, "label").map((pack) => [pack.key, pack]);
    this.choices = new foundry.utils.Collection(orderedPacks);
  }
}

export class TagFilter extends CheckboxFilter {
  static label = "PF1.Tags";
  static indexField = "system.tags";
}

export class SourceFilter extends CheckboxFilter {
  /** @override */
  static label = "PF1.Source";

  /**
   * This filter requests `system.sources` from the index, but operates on `__source` in the entry data
   * to support sources without an `id` (e.g. homebrew sources, or others providing only a title).
   */
  static indexField = "system.sources";

  /** @override */
  static defaultBooleanOperator = BOOLEAN_OPERATOR.OR;

  /** @override */
  static booleanOperator = false;

  /** @override */
  prepareChoices() {
    class SourceEntry {
      id;
      get key() {
        return this.id;
      }

      label;

      count = 0;

      get hidden() {
        return this.count === 0;
      }

      constructor(id, label) {
        this.id = id;
        this.label = label;
      }
    }

    const registrySources = pf1.registry.sources.map((source) => new SourceEntry(source.id, source.name));
    const collection = new foundry.utils.Collection(registrySources.map((choice) => [choice.key, choice]));

    // Create tag used as alternative to ensure multiple sources having same source don't do duplicates
    const placeholderId = (s) =>
      "__unknown_" + pf1.utils.createTag(s, { allowUpperCase: true, allowInitialNumbers: true });

    for (const entry of this.compendiumBrowser.entries) {
      entry.__source ??= entry.system?.sources ?? [];
      const sources = entry.__source
        .filter((source) => source.id || source.title)
        .map((source) => ({
          ...source,
          id: source.id || placeholderId(source.title),
        }));

      // Add custom entries
      for (const source of sources) {
        const choice = collection.get(source.id);
        if (!choice) {
          collection.set(source.id, new SourceEntry(source.id, source.title));
        } else choice.count += 1;
      }
    }

    this.choices = collection;
  }

  /** @override */
  applyFilter(entry) {
    const data = (foundry.utils.getProperty(entry, this.constructor.indexField) ?? []).map((source) => source.id);
    const testMethod = this.booleanOperator === BOOLEAN_OPERATOR.OR ? "some" : "every";
    return this.choices
      .filter((choice) => choice.active)
      .map((choice) => choice.key)
      [testMethod]((key) => data.includes(key));
  }
}
