  /**
   * Prepare duplicate item data
   *
   * @param {object} itemData
   * @param {object} options
   * @param {boolean} [options.rename]
   * @param {ItemPF} [options.original]
   */
  _prepareDuplicateItem(itemData, { rename = true, original } = {}) {
    delete itemData._id;

    delete itemData.system.links?.children;
    delete itemData.system.links?.charges;

    itemData.sort = itemData.sort + 1_000;

    if (original) {
      itemData._stats ??= {};
      itemData._stats.duplicateSource ||= original.uuid;
    }

    if (rename) {
      // Build list of visible names in same general category.
      const subType = itemData.system?.subType;
      const relatedItems = this.actor.items.filter(
        (i) =>
          i.type === itemData.type &&
          (subType ? subType === i.system?.subType : true) &&
          (itemData.type === "spell" ? itemData.system.level === i.system.level : true)
      );

      const searchUnusedName = (name, existingNames) => {
        name = name.replace(/\s+\(\d+\)$/, "");
        let iter = 1;
        let newName;
        do {
          iter += 1;
          newName = `${name} (${iter})`;
        } while (existingNames.has(newName));
        return newName;
      };

      // Alter identified name
      const identNames = new Set(relatedItems.map((i) => i.name)); // Explicit real name
      itemData.name = searchUnusedName(itemData.name, identNames);

      // Alter unidentified name if it exists
      const unidentified = itemData.system.unidentified;
      if (unidentified?.name && CONFIG.Item.documentClasses[itemData.type]?.isPhysical) {
        const unidentNames = new Set(relatedItems.map((i) => i.getName(true))); // Player visible names instead of unidentified names
        unidentified.name = searchUnusedName(unidentified.name, unidentNames);
      }
    }
  }

