  /**
   * Handle deleting an existing Owned Item for the Actor
   *
   * @private
   * @param {Event} event   The originating click event
   * @returns {Promise<*>} - Promise for when the interaction completes
   */
  async _onItemDelete(event) {
    event.preventDefault();

    const button = event.currentTarget;
    if (button.disabled) return;

    const li = event.currentTarget.closest(".item");
    const item = this.actor.items.get(li.dataset.itemId);

    button.disabled = true;

    if (getSkipActionPrompt()) return item.delete();

    // Find linked items that would be deleted with this
    const linked = new Set();
    /**
     * @param {ItemPF} item
     */
    const findAllLinks = (item) => {
      linked.add(item);
      const links = item.getLinkedItemsSync("children");
      for (const i of links) {
        if (linked.has(i)) continue;
        findAllLinks(i);
      }
    };
    findAllLinks(item);
    // Find class associations
    if (item.type === "class") {
      const associations = item.getFlag("pf1", "links.classAssociations") ?? {};
      for (const itemId of Object.keys(associations)) {
        const item = this.actor.items.get(itemId);
        if (item) linked.add(item);
      }
    }
    linked.delete(item); // Don't notify about self

    const templateData = {
      linked: [...linked].map((i) => ({
        name: i.name,
        uuid: i.uuid,
        type: game.i18n.localize(CONFIG.Item.typeLabels[i.type]),
      })),
    };

    const msg = await renderTemplate("systems/pf1/templates/internal/delete-item.hbs", templateData);

    const confirm = await foundry.applications.api.DialogV2.confirm({
      window: { title: game.i18n.format("PF1.DeleteItemTitle", { name: item.name }), icon: "fa-solid fa-trash" },
      classes: ["pf1-v2", "delete-item"],
      content: msg,
      rejectClose: false,
      modal: true, // Require dialog to be resolved
    });

    if (confirm) item.delete();

    button.disabled = false;
  }

  async _onItemGive(event) {
    event.preventDefault();

    const itemId = event.currentTarget.closest(".item").dataset.itemId;
    const item = this.actor.items.get(itemId);

    const targets = game.actors.filter(
      (a) => a !== this.actor && a.testUserPermission(game.user, CONST.DOCUMENT_OWNERSHIP_LEVELS.LIMITED)
    );
    if (targets.length === 0) ui.notifications.warn("PF1.Error.NoGiftTargets", { localize: true });

    const targetActorId = await pf1.utils.dialog.getActor({
      window: {
        title: game.i18n.localize("PF1.GiveItemToActor"),
      },
      actors: targets,
    });

    const target = game.actors.get(targetActorId);
    if (!target) throw new Error(`Invalid actor ID as gift target: "${targetActorId}"`);

    if (target.isOwner) {
      const itemData = item.toObject();
      delete itemData.system?.links?.children;
      const newItem = await Item.implementation.create(itemData, { parent: target });
      // Delete only if item was successfully created
      if (newItem) await item.delete();
    } else {
      game.socket.emit("system.pf1", {
        eventType: "giveItem",
        targetActor: target.uuid,
        item: item.uuid,
      });
      // Deleting will be performed on the gm side as well to prevent race conditions
    }
  }

  async _onItemSplit(event) {
    event.preventDefault();

    const itemId = event.currentTarget.closest(".item").dataset.itemId;
    const item = this.actor.items.get(itemId);

    const quantity = item.system.quantity;
    if (quantity < 2) throw new Error("Can't split stack with less than 2 items");

    const options = {
      total: quantity,
      title: game.i18n.format("PF1.Dialog.SplitItem.Title", { name: item.name }),
    };

    // Position the dialog near click location
    const clickEl = event.target.getBoundingClientRect();
    const renderOpts = {
      position: {
        width: 320,
        left: clickEl.left - 350,
        top: clickEl.top - 80,
      },
    };

    const result = await pf1.applications.SplitStack.wait(options, renderOpts);
    if (!result) return;

    const [keep, split] = result;

    const itemData = item.toObject();
    itemData.system.quantity = split;

    this._prepareDuplicateItem(itemData, { rename: false, original: item });

    await Item.implementation.createDocuments([itemData], { parent: this.actor });
    await item.update({ "system.quantity": keep });
  }

