import { CompactingMixin } from "@models/mixins/compacting-mixin.mjs";
import { PreparedModel } from "@models/abstract/prepared-model.mjs";
import { IDField } from "@datafields/id-field.mjs";

import { ReplaceableSourceMixin } from "@models/mixins/replaceable-source.mjs";

import { FormulaField } from "@datafields/formula-field.mjs";

/**
 * Conditional modifier bundle.
 */
export class ItemConditional extends ReplaceableSourceMixin(CompactingMixin(PreparedModel)) {
  /**
   * Metadata mimicking Foundry documents
   *
   * @internal
   * @readonly
   */
  static metadata = Object.freeze({
    name: "Conditional",
    label: "Conditional", // TODO: i18n
  });

  constructor(data, options) {
    if (options instanceof pf1.components.ItemAction) {
      foundry.utils.logCompatibilityWarning(
        "ItemConditional constructor's second parameter as parent is deprecated. Please wrap it in options object like with datamodels.",
        {
          since: "PF1 v11",
          until: "PF1 v12",
        }
      );
      options = { parent: options };
    }
    super(data, options);
  }

  static defineSchema() {
    const fields = foundry.data.fields;

    return {
      _id: new IDField(),
      name: new fields.StringField(),
      default: new fields.BooleanField({ initial: false }),
      modifiers: new fields.ArrayField(new fields.EmbeddedDataField(ItemConditionalModifier)),
    };
  }

  /**
   * Retrieve UUID
   *
   * Relies on {@link pf1.component.ItemAction.getEmbeddedDocument | ItemAction#getEmbeddedDocument()} to function with fromUuid() and similar functions.
   *
   * @type {string}
   */
  get uuid() {
    return this.parent.uuid + `.${this.constructor.metadata.name}.${this.id}`;
  }

  /**
   * For supporting modifiers in fromUuid()
   *
   * Synced with Foundry v12.331
   *
   * @param {string} embeddedName
   * @param {string} id
   * @param {object} options
   * @returns {pf1.components.ItemConditionalModifier|undefined}
   */
  getEmbeddedDocument(embeddedName, id, options = {}) {
    let result;
    switch (embeddedName) {
      case "Modifier": {
        result = this.modifiers?.get(id);
      }
    }

    if (options.strict) throw new Error(`${embeddedName} id [${id}] does not exist in ${this.uuid}`);

    return result;
  }

  _configure(options) {
    super._configure(options);

    // Following prevent these definitions being lost on model reset()
    Object.defineProperties(this, {
      // Modifiers collection cache to avoid conflicts with stored array
      _modifiers: {
        value: new Collection(),
        writable: false,
        enumerable: false,
      },
    });
  }

  /**
   * @internal
   */
  prepareData() {
    this._prepareModifiers();
  }

  /**
   * @internal
   */
  _prepareModifiers() {
    /** @type {Collection<ItemConditionalModifier>} */
    const collection = this._modifiers;
    const prior = new Collection(collection.entries());
    collection.clear(); // TODO: Remove specific entries after the loop instead of full clear here

    // HACK: this causes modifier datamodels to be re-initialized
    // It would be better for the system if we didn't make this persistent reference guarantee
    for (const mod of this.modifiers) {
      let modifier = null;
      if (prior && prior.has(mod.id)) {
        modifier = prior.get(mod.id);
        if (mod !== modifier) modifier.replaceSource(mod._source);
      } else {
        modifier = mod;
      }

      collection.set(modifier.id, modifier);
    }

    this.modifiers = collection;
  }

  /**
   * Create new conditional
   *
   * @param {object|object[]} data - Data to create conditional(s) from.
   * @param {object} context - Context data
   * @param {ItemAction} context.parent Parent action to add the conditional to.
   * @throws {Error} - If no valid parent is defined.
   * @returns {Proimise<ItemConditional[]>} - Created conditionals
   */
  static async create(data, context = {}) {
    const { parent } = context;

    if (!(parent instanceof pf1.components.ItemAction))
      throw new Error("Can not create conditionals without parent ItemAction");

    if (!Array.isArray(data)) data = [data];

    // Prepare data
    data = data.map((dataObj) => new this(dataObj).toObject());
    const conditionals = parent.toObject().conditionals || [];
    conditionals.push(...data);

    // Update parent
    await parent.update({ conditionals });

    // Return results
    return data.map((o) => parent.conditionals.get(o._id));
  }

  static get defaultData() {
    foundry.utils.logCompatibilityWarning("ItemConditional.defaultData has been deprecated with no replacement.", {
      since: "PF1 v11",
      until: "PF1 v12",
    });

    return this.schema.getInitialValue();
  }

  /** @type {string} */
  get id() {
    return this._id;
  }

  /**
   * Update conditional
   *
   * @param {object} updateData
   * @param {object} options
   */
  async update(updateData, options = {}) {
    if (options.dryRun) return this.updateSource(updateData, { dryRun: true });

    this.updateSource(updateData);

    const conditionals = this.parent.conditionals.map((t) => t.toObject());
    await this.parent.update({ conditionals });
  }

  /**
   * Delete conditional
   */
  async delete() {
    const conditionals = this.parent.conditionals.map((t) => t.toObject());
    conditionals.findSplice((c) => c._id === this.id);
    return this.parent.update({ conditionals });
  }

  /**
   * @internal
   * @param {object} data
   */
  static pruneData(data) {
    if (!data.default) delete data.default;
    if (!(data.modifiers?.length > 0)) delete data.modifiers;
    else {
      for (const m of data.modifiers) {
        ItemConditionalModifier.pruneData(m);
      }
    }
  }
}

/**
 * Individual modifier in a conditional bundle.
 */
export class ItemConditionalModifier extends ReplaceableSourceMixin(CompactingMixin(foundry.abstract.DataModel)) {
  /**
   * Metadata mimicking Foundry documents
   *
   * @internal
   * @readonly
   */
  static metadata = Object.freeze({
    name: "Modifier",
    label: "Modifier", // TODO: i18n
  });

  /**
   * @param {object} data
   * @param {object} options
   */
  constructor(data, options) {
    if (options instanceof pf1.components.ItemAction) {
      foundry.utils.logCompatibilityWarning(
        "ItemConditionalModifier constructor's second parameter as parent is deprecated. Please wrap it in options object like with datamodels.",
        {
          since: "PF1 v11",
          until: "PF1 v12",
        }
      );
      options = { parent: options };
    }
    super(data, options);
  }

  static defineSchema() {
    const fields = foundry.data.fields;

    return {
      _id: new IDField(),
      formula: new FormulaField(),
      target: new fields.StringField(), // modifier on what?
      subTarget: new fields.StringField(), // which attack is this targeting?
      type: new fields.StringField({ initial: "untyped" }), // Bonus type
      damageType: new fields.SetField(new fields.StringField({ blank: false, nullable: false })),
      critical: new fields.StringField({ initial: undefined }), // Does this target normal or critical confirm? or which kind of damage is this in relation to crits?
    };
  }

  /**
   * Retrieve UUID
   *
   * Relies on {@link pf1.component.ItemConditional.getEmbeddedDocument | ItemConditional#getEmbeddedDocument()} to function with fromUuid() and similar functions.
   *
   * @type {string}
   */
  get uuid() {
    return this.parent.uuid + `.${this.constructor.metadata.name}.${this.id}`;
  }

  static migrateData(source) {
    if (source.damageType && !Array.isArray(source.damageType)) {
      const custom =
        source.damageType.custom
          ?.split(";")
          .map((t) => t?.trim())
          .filter((t) => !!t) ?? [];
      const standard = source.damageType.values ?? [];
      source.damageType = [...standard, ...custom];
    }

    // Move effect.dc and effect.cl to dc and cl
    if (source.target === "effect") {
      if (["dc", "cl"].includes(source.subTarget)) {
        source.target = source.subTarget;
        source.subTarget = undefined;
      }
    }
    // Remove redundant subtarget
    if (["cl", "dc"].includes(source.target)) source.subTarget = undefined;
    // Move misc.charges to charges
    if (source.target === "misc") {
      if (source.subTarget === "charges") {
        source.target = source.subTarget;
        source.subTarget = undefined;
      }
    }

    // Initialize defaults for attack and damage modifiers if not present
    if (source.target === "damage") {
      if (!source.subTarget) source.subTarget = "allDamage";
      if (!source.critical) source.critical = "normal";
    }
    if (source.target === "attack") {
      if (!source.subTarget) source.subTarget = "allAttack";
      if (!source.critical) source.critical = "normal";
    }

    return super.migrateData(source);
  }

  static get defaultData() {
    foundry.utils.logCompatibilityWarning(
      "ItemConditionalModifier.defaultData has been deprecated with no replacement.",
      {
        since: "PF1 v11",
        until: "PF1 v12",
      }
    );

    return this.schema.getInitialValue();
  }

  /** @type {string} */
  get id() {
    return this._id;
  }

  /**
   * Part ID string
   *
   * @type {string}
   */
  get partID() {
    const parts = [this.target];
    if (this.subTarget) parts.push(this.subTarget);
    if (this.critical) parts.push(this.critical);
    return parts.join(".");
  }

  /**
   * Create conditional modifier
   *
   * @param {object} data - Creation data
   * @param {object} context - Context data
   * @param {ItemConditional} context.parent - Parent conditional
   * @returns {Promise<ItemConditionalModifier[]>} - Created modifiers
   */
  static async create(data, context = {}) {
    const { parent } = context;
    if (!(parent instanceof pf1.components.ItemConditional))
      throw new Error("Conditional modifier's parent must be a Conditional");

    if (!Array.isArray(data)) data = [data];

    const modifiers = parent.toObject().modifiers ?? [];

    // Prepare data
    data = data.map((dataObj) => new this(dataObj).toObject());
    modifiers.push(...data);

    // Update parent
    await parent.update({ modifiers });

    // Return results
    return data.map((o) => parent.modifiers.get(o._id));
  }

  /**
   * Update modifier
   *
   * @param {object} updateData
   * @param {object} [options]
   * @returns {Promise<ItemAction>} - Updated action
   */
  async update(updateData, options = {}) {
    if (options.dryRun) return this.updateSource(updateData, { dryRun: true });

    this.updateSource(updateData);

    const modifiers = this.parent.modifiers.map((m) => m.toObject());
    return this.parent.update({ modifiers });
  }

  /**
   * Delete this individual modifier.
   *
   * @returns {Promise<ItemAction>} - Updated action
   */
  async delete() {
    const modifiers = this.parent.toObject().modifiers ?? [];
    const idx = modifiers.findIndex((m) => m._id === this.id);
    if (idx < 0) throw new Error(`Modifier not found in parent ${this.parent.name}`);

    modifiers.splice(idx, 1);
    return this.parent.update({ modifiers });
  }

  /**
   * @internal
   * @param {object} data
   */
  static pruneData(data) {
    if (!data.type) delete data.type;
    if (!data.formula) delete data.formula;
    if (!data.critical || data.critical === "normal") delete data.critical;
    if (!data.target) delete data.target;
    if (!data.subTarget) delete data.subTarget;

    if (!["attack", "damage"].includes(data.target)) {
      delete data.critical;
    }

    // Damage type is only meaningful with damage target
    if (data.target !== "damage") delete data.damageType;
    else {
      if (data.damageType?.length) {
        data.damageType = [...new Set(data.damageType)].filter((t) => !!t);
      }
      if (!(data.damageType?.length > 0)) delete data.damageType;
    }

    // These don't use bonus typing
    if (["damage", "size", "charges", "critMult", "cl", "sl"].includes(data.target)) {
      delete data.type;
    }
  }
}
