/**
 * `init` hook handler.
 */
export function register() {
  if (game.release.generation < 13) return;

  // Golarian calendar is largely identical to Gregorian calendar
  // Source: https://paizo.com/community/blog/v5748dyo5la5m?Time-on-GolarionDays-of-the-Week
  const gregorian = CONFIG.time.earthCalendarConfig;
  const calendar = foundry.utils.deepClone(gregorian);

  calendar.name = "PF1.Calendar.Label";
  delete calendar.description;

  calendar.days.values = calendar.days.values.map((day) => ({
    ...day, // Copy Gregorian data
    name: `PF1.Calendar.Days.${day.ordinal}.Name`,
    abbreviation: undefined, // `PF1.Calendar.Days.${i}.Abbr`, // No official abbreviations
  }));

  calendar.months.values = calendar.months.values.map((month) => ({
    ...month, // Copy Gregorian data
    name: `PF1.Calendar.Months.${month.ordinal}.Name`,
    abbreviation: undefined, // `PF1.Calendar.Months.${i}.Abbr`, // No official abbreviations
  }));

  // Defined inline to support v12
  class GolarianCalendarData extends foundry.data.CalendarData {
    /**
     * Earth year offset
     */
    static YEAR_OFFSET = 2700;

    /**
     * Set world time based on Earth time
     *
     * @example
     * Set current date
     * ```js
     * const d = new Date();
     * const day = d.getDate();
     * const year = d.getFullYear();
     * const month = d.getMonth();
     * game.time.calendar.set({ day, year, month, hour:12, minute:0, second:0 });
     * ```
     *
     * @experimental
     * @param {object} components - Components as accepted by `game.time.set()`
     * @returns {Promise<*>} - Result from `game.time.set(...)`
     */
    async setEarthTime(components = {}) {
      if ("year" in components) components.year += this.constructor.YEAR_OFFSET;
      return game.time.set(components);
    }

    /**
     * Synchronizes world time with canonical time in terms of Earth-Golarion equivalency.
     *
     * Hours, minutes and seconds are preserved from current world time unless explicitly given.
     *
     * @param {object} [options] - Additional options
     * @param {number} [options.hour] - Exact hours
     * @param {number} [options.minute] - Exact minutes
     * @param {number} [options.second] - Exact seconds
     * @experimental
     * @returns {Promise<*>} - Result from `game.time.set(...)`
     */
    async syncCanonical({ hour, minute, second } = {}) {
      // Preserve part of old time if undefined
      const oldtime = game.time.components;
      hour ??= oldtime.hour;
      minute ??= oldtime.minute;
      second ??= oldtime.second;

      // Determine new world time in format Foundry expects
      const d = new Date();
      const year = d.getFullYear();
      const gregorian = game.time.earthCalendar;
      const isLeapYear = gregorian.isLeapYear(year);
      const month = d.getMonth();
      // Foundry tracks days from start of year, months are untracked in data form
      let day = d.getDate(); // Day of current month
      const months = gregorian.months.values;
      for (let i = 0; i < month; i++) {
        if (isLeapYear) day += months[i].leapDays ?? months[i].days;
        else day += months[i].days;
      }

      return this.setEarthTime({ day, year, hour, minute, second });
    }
  }

  CONFIG.time.golarianCalendarConfig = calendar;
  CONFIG.time.golarianCalendarClass = GolarianCalendarData;

  // Register as active world calendar as expected by Foundry
  CONFIG.time.worldCalendarConfig = calendar;
  CONFIG.time.worldCalendarClass = GolarianCalendarData;

  console.debug("PF1 | Golarian calendar registered");
}

/**
 * `i18nInit` hook handler to localize calendar strings
 */
export function localize() {
  if (!CONFIG.time.golarianCalendarConfig) return; // In case registering failed or was disabled for some reason

  const calendar = CONFIG.time.golarianCalendarConfig;

  calendar.name = game.i18n.localize(calendar.name);
  calendar.description = game.i18n.localize(calendar.description);

  /*
  // Core calendar does not actually have the following localized
  for (const month of calendar.months.values) {
    month.name = game.i18n.localize(month.name);
    if (month.abbreviation) month.abbreviation = game.i18n.localize(month.abbreviation);
  }
  for (const day of calendar.days.values) {
    day.name = game.i18n.localize(day.name);
    if (day.abbreviation) day.abbreviation = game.i18n.localize(day.abbreviation);
  }
  */
}

/**
 * `setup` hook handler, when `game.time` has been initialized.
 */
export function finalize() {
  if (!CONFIG.time.golarianCalendarConfig) return; // In case registering failed or was disabled for some reason

  // Provide initialized golarian calendar even if something overrides world calendar
  game.time.golarianCalendar = new CONFIG.time.golarianCalendarClass(CONFIG.time.golarianCalendarConfig, {
    strict: true,
  });
}
