export class ItemDirectoryPF extends CONFIG.ui.items {
  /**
   * v12 and prior
   *
   * Enrich default options for detecting identified/unidentified name changes
   *
   * @override
   */
  static get defaultOptions() {
    const options = super.defaultOptions;
    options.renderUpdateKeys.push("system.unidentified.name", "system.identified");
    return options;
  }

  // v13
  static DEFAULT_OPTIONS = {
    renderUpdateKeys: ["system.unidentified.name", "system.identified"],
  };
}
