const buttons = {
  spells: { label: "PF1.BrowseSpells" },
  items: { label: "PF1.BrowseItems" },
  bestiary: { label: "PF1.BrowseBestiary" },
  feats: { label: "PF1.BrowseFeats" },
  classes: { label: "PF1.BrowseClasses" },
  races: { label: "PF1.BrowseRaces" },
  buffs: { label: "PF1.BrowseBuffs", wide: true },
};

/**
 * @param {Event} event - Triggering event
 */
function compendiumButtonClick(event) {
  event.preventDefault();
  const type = event.target.dataset.category;
  pf1.applications.compendiums[type]._render(true, { focus: true });
}

/**
 * Render compendium browser buttons.
 *
 * @param {CompendiumDirectory} app
 * @param {HTMLElement|JQuery} html
 * @param {object} options
 */
Hooks.on("renderCompendiumDirectory", async (app, html) => {
  if (html instanceof jQuery) html = html[0]; // v12/v13 cross compatibility

  if (html.querySelector("section.pf1.action-buttons")) return; // Avoid duplicating the buttons

  let element;
  if (game.release.generation >= 13) {
    element = document.createElement("section");
    html.append(element);
  } else {
    element = html.querySelector("footer.directory-footer");
  }
  element.classList.add("pf1", "action-buttons");
  if (game.release.generation >= 13) element.classList.add("v13");

  for (const [category, info] of Object.entries(buttons)) {
    const button = document.createElement("button");
    button.type = "button";
    button.dataset.category = category;
    button.classList.add("compendium", category);
    button.innerText = game.i18n.localize(info.label);
    if (info.wide) button.classList.add("colspan-2");
    element.append(button);
    button.addEventListener("click", compendiumButtonClick);
  }
});

// Add compendium sidebar context options
function toggleCompendiumVisibility(html, entries) {
  // Add option to enable & disable pack
  entries.unshift(
    {
      name: game.i18n.localize("PF1.CompendiumBrowser.HidePack"),
      icon: '<i class="fa-solid fa-low-vision"></i>',
      condition: (li) => {
        if (li instanceof jQuery) li = li[0]; // v12/v13 cross-compatibility
        const pack = game.packs.get(li.dataset.pack);
        return pack.config.pf1?.disabled !== true;
      },
      callback: (li) => {
        if (li instanceof jQuery) li = li[0]; // v12/v13 cross-compatibility
        const pack = game.packs.get(li.dataset.pack);
        pack.configure({ pf1: { disabled: true } });
      },
    },
    {
      name: game.i18n.localize("PF1.CompendiumBrowser.ShowPack"),
      icon: '<i class="fa-solid fa-eye"></i>',
      condition: (li) => {
        if (li instanceof jQuery) li = li[0]; // v12/v13 cross-compatibility
        const pack = game.packs.get(li.dataset.pack);
        return pack.config.pf1?.disabled === true;
      },
      callback: (li) => {
        if (li instanceof jQuery) li = li[0]; // v12/v13 cross-compatibility
        const pack = game.packs.get(li.dataset.pack);
        pack.configure({ pf1: { disabled: false } });
      },
    }
  );
}

/**
 * @param {CompendiumDirectory} app
 * @param {HTMLElement} html
 */
function displayCompendiumVisibility(app, html) {
  if (html instanceof jQuery) html = html[0]; // v12/v13 cross-compatibility

  for (const li of html.querySelectorAll("li[data-pack]")) {
    const pack = game.packs.get(li.dataset.pack);
    const hidden = pack.config.pf1?.disabled === true;
    if (!hidden) continue;

    const el = li.querySelector(".status-icons");
    const hiddenIcon = document.createElement("i");
    hiddenIcon.classList.add("fa-solid", "fa-filter-circle-xmark");
    hiddenIcon.dataset.tooltip = "PF1.Hidden"; // TODO: Add better tooltip
    hiddenIcon.ariaLabel = game.i18n.localize("PF1.Hidden");
    el.prepend(hiddenIcon);
  }
}

Hooks.on("getCompendiumDirectoryEntryContext", toggleCompendiumVisibility); // v12
Hooks.on("getCompendiumContextOptions", toggleCompendiumVisibility); // v13
Hooks.on("renderCompendiumDirectory", displayCompendiumVisibility);
