import { Registry, RegistryEntry } from "./base-registry.mjs";

import { ItemChange } from "@component/change.mjs";

const omitFields = (obj, keys) => {
  for (const key of keys) delete obj[key];
  return obj;
};

/**
 * Condition's status HUD behaviour configuration.
 *
 * @group Conditions
 */
export class StatusHudModel extends foundry.abstract.DataModel {
  static defineSchema() {
    const fields = foundry.data.fields;

    return {
      show: new fields.BooleanField({ required: false, initial: true, label: "Show in token status HUD" }),
      include: new fields.SetField(new fields.StringField({ choices: () => Object.keys(game.model.Actor) }), {
        initial: [],
        required: false,
        label: "Required actor types.",
      }),
      exclude: new fields.SetField(new fields.StringField({ choices: () => Object.keys(game.model.Actor) }), {
        initial: [],
        required: false,
        label: "Excluded actor types. Has no effect if include list is populated.",
      }),
    };
  }

  /**
   * Allowed actor types.
   *
   * @remarks
   * - Undefined if no limitations are present.
   *
   * @type {Array<string>|undefined}
   */
  get actorTypes() {
    if (this.include.size) return [...this.include];
    if (this.exclude.size) return Object.keys(game.model.Actor).filter((type) => !this.exclude.has(type));
    return undefined;
  }
}

/**
 * A single condition entry in the {@link Condition} registry.
 *
 * @group Conditions
 */
export class Condition extends RegistryEntry {
  static defineSchema() {
    const fields = foundry.data.fields;

    return {
      ...super.defineSchema(),
      aliases: new fields.SetField(new fields.StringField({ blank: false, nullable: false })),
      texture: new fields.StringField({ required: true }),
      track: new fields.StringField({
        required: false,
        blank: false,
        nullable: true,
        initial: null,
        choices: () => Conditions.TRACKS,
      }),
      mechanics: new fields.SchemaField({
        changes: new fields.ArrayField(
          new fields.SchemaField({ ...omitFields(ItemChange.defineSchema(), ["_id", "value", "flavor"]) })
        ),
        flags: new fields.SetField(new fields.StringField({ blank: false })),
        contexts: new fields.SetField(new fields.StringField({ blank: false })),
      }),
      journal: new fields.StringField({ nullable: true, blank: false, required: false }),
      showInDefense: new fields.BooleanField({ required: false, initial: true }),
      showInAction: new fields.BooleanField({ required: false, initial: true }),
      showInBuffsTab: new fields.BooleanField({ required: false, initial: true }),
      hud: new fields.EmbeddedDataField(StatusHudModel),
      statuses: new fields.SetField(new fields.StringField()), // Secondary statuses
    };
  }

  /**
   * Convert condition to format expected by `CONFIG.statusEffects`.
   *
   * @returns {object} - Base object
   */
  toStatusEffect() {
    return {
      id: this.id,
      name: this.name,
      img: this.texture,
      hud: this.hud.show ? this.hud : false,
    };
  }
}

