/**
 * @param {JournalEntryPage} page
 * @param {object} context
 */
function addHeaders(page, context) {
  // Find parent pages if deeper than level 1 heading
  let maxLevel = page.title.level - 1;
  if (maxLevel <= 0) return;

  /** @type {JournalEntry} */
  const parent = page.parent;
  if (!(parent instanceof JournalEntry)) return;

  /** @type {JournalEntryPage[]} */
  const pages = parent.pages.contents.sort((a, b) => a.sort - b.sort);

  const idx = pages.indexOf(page);
  if (idx <= 0) return;

  const headers = [];
  // Traverse backwards
  for (let i = idx - 1; i >= 0; i--) {
    const npage = pages[i];
    const nlevel = npage.title.level;
    if (nlevel <= maxLevel) {
      maxLevel = nlevel - 1;
      headers[maxLevel] = npage;
      if (maxLevel < 1) break;
    }
  }

  context.pf1 = { headers };
}

/**
 * Journal text page sheet that displays parentage with sub-pages
 *
 * @deprecated
 * - Will be replaced with AppV2
 */
export class JournalTextPageSheetPF1 extends JournalTextPageSheet {
  get template() {
    if (this.document.type !== "text" || this.isEditable) return super.template;
    return "systems/pf1/templates/journals/text-view.hbs";
  }

  async getData() {
    const context = await super.getData();

    addHeaders(this.document, context);

    // V13 compatibility shims
    context.text ??= {
      enriched: context.editor?.content,
    };

    return context;
  }
}

function unregisterV1Sheet() {
  // Unregister the old, in separate function to avoid strict JS problems
  DocumentSheetConfig.unregisterSheet(JournalEntryPage, "pf1", JournalTextPageSheetPF1);
}

function registerV2Sheet() {
  // JournalEntryPageTextSheet is wrong despite looking correct, so we extend prosemirror sheet instead
  // Identical name to above match sheet registration, masks the above name
  class JournalTextPageSheetPF1 extends foundry.applications.sheets.journal.JournalEntryPageProseMirrorSheet {
    /** @override */
    static VIEW_PARTS = {
      content: {
        template: "systems/pf1/templates/journals/text-view.hbs",
      },
    };

    /** @override */
    async _prepareContext(options) {
      const context = await super._prepareContext(options);

      addHeaders(this.document, context);

      // V12 compatibility shims
      context.data ??= {};
      context.data.title = context.title;

      return context;
    }
  }

  DocumentSheetConfig.registerSheet(JournalEntryPage, "pf1", JournalTextPageSheetPF1, {
    types: ["text"],
    makeDefault: false,
    label: "PF1.Sheet.TextPage",
  });
}

export function registerV13PageSheet() {
  if (game.release.generation >= 13) {
    unregisterV1Sheet();
    registerV2Sheet();
  }
}
