/**
 * Wrapper to register Token Ruler for Foundry v13 in a way that does not break v12 support.
 *
 * TODO:
 * - Refactor after v12 support is removed
 */
export function registerTokenRuler() {
  if (!(game.release.generation >= 13)) return;

  class TokenRulerPF extends CONFIG.Token.rulerClass {
    //    static WAYPOINT_LABEL_TEMPLATE = "systems/pf1/templates/hud/waypoint-label.hbs";

    /** @override */
    /*
    _getWaypointLabelContext(waypoints) {
      const data = super._getWaypointLabelContext(waypoints);

      const tokenDoc = this.token.document;
      const actor = tokenDoc?.actor;
      if (!actor) return data;

      const scale = this.token.scene.grid.distance;

      for (const wpd of data) {
        this.applyWaypointSpeedData(wpd.waypoint, wpd.waypoint.measurement.cost / scale);
      }

      return data;
    }
    */

    /**
     * @experimental
     * @param {string} type
     * @returns {MovementInfo|undefined}
     */
    getMovement(type) {
      /** @type {TokenDocumentPF} */
      const tokenDoc = this.token.document;
      /** @type {ActorPF} */
      const actor = tokenDoc?.actor;

      const move = actor.getMovement(type);
      if (!move) return;

      // Adjust speed to be in steps
      move.speed /= pf1.const.step;

      // TODO: Homebrew for run speed modifier
      //actor.changeFlags.noHeavyEncumbrance
      //actor.changeFlags.heavyArmorFullSpeed

      return move;
    }

    /**
     * @internal
     * @param {object} wp - Waypoint
     * @param {number} cost - Steps
     */
    applyWaypointSpeedData(wp, cost) {
      const move = this.getMovement(wp.action);
      const baseSpeed = move.speed;

      // TODO: Metric support
      let factor = baseSpeed > 0 ? Math.ceil(cost / baseSpeed) : 10;
      // Explicit max speed
      if (factor > move.max) factor = 10;
      // Factor jumps, ignore max speed since these are for formatting
      if (move.factors) factor = move.factors[factor] ?? factor;

      wp.speed = {
        step: cost <= 5,
        factor,
        factorBound: Math.clamp(factor, 0, move.max),
        factorBase: Math.ceil(cost / baseSpeed),
        singleMove: factor <= 1,
        doubleMove: factor <= 2 && factor > 1,
        running: factor > 2,
        runningFar: factor > 4, // Run feat
        exceedMovement: factor > 5, // Exceeding move speed limits
      };
    }

    /** @override */
    /*
    _getSegmentStyle(waypoint) {
      const { width, color, alpha } = super._getSegmentStyle(waypoint);

      return { width, color, alpha };
    }
    */

    /** @override */
    _getGridHighlightStyle(waypoint, offset) {
      if (!this.token.actor) return super._getGridHighlightStyle(waypoint, offset);

      // TODO: Fall back to default if scene config is weird
      // return super._getGridHighlightStyle(waypoint, offset);

      const move = this.getMovement(waypoint.action);
      if (!move) return super._getGridHighlightStyle(waypoint, offset);

      const totalCost = waypoint.measurement.cost;
      const scale = this.token.scene.grid.distance;
      const scaledCost = totalCost / scale;

      if (totalCost == 0) {
        // TODO: Detect immobility by other means than lacking move speed
        if (move.speed == 0) return pf1.config.tokenRuler.grid.immobile;
        return pf1.config.tokenRuler.grid.start;
      }

      this.applyWaypointSpeedData(waypoint, scaledCost);

      const factor = scaledCost <= 1 && move.speed > 0 && move.step ? 0 : waypoint.speed.factor;

      switch (factor) {
        case 0:
          return pf1.config.tokenRuler.grid.step;
        case 1: // Single move
          return pf1.config.tokenRuler.grid.move;
        case 2: // Double move
          return pf1.config.tokenRuler.grid.move2;
        case 3: // Run
          return pf1.config.tokenRuler.grid.move3;
        case 4: // Run
          return pf1.config.tokenRuler.grid.run;
        case 5: // Run feat
          return pf1.config.tokenRuler.grid.run2;
        default: // Exceed movement
          return pf1.config.tokenRuler.grid.exceed;
      }
    }
  }

  CONFIG.Token.rulerClass = TokenRulerPF;

  // Movement action details
  // Foundry defaults climb, crawl and jump to use double movement
  CONFIG.Token.movement.actions.climb.getCostFunction = () => (cost) => cost;
  CONFIG.Token.movement.actions.crawl.getCostFunction = () => (cost) => cost;
  CONFIG.Token.movement.actions.jump.getCostFunction = () => (cost) => cost;
}
