/**
 * Base actor class with minimal functionality.
 *
 * Provides only caching of .itemTypes and nothing else.
 */
export class ActorBasePF extends Actor {
  /**
   * Add default artwork.
   *
   * @see {@link pf1.config.defaultIcons.actors}
   *
   * @internal
   * @override
   * @param {object} [actorData] - Actor data to guide creation if any
   * @returns {object} - Resulting data alterations
   */
  static getDefaultArtwork(actorData) {
    const result = super.getDefaultArtwork(actorData);
    const image = pf1.config.defaultIcons.actors[actorData?.type];
    if (image) {
      result.img = image;
      result.texture.src = image;
    }
    return result;
  }

  /**
   * @override
   */
  reset() {
    // Reset item types cache
    this._itemTypes = null;
    // Reset roll data cache if it exists.
    this._rollData = null;

    this._initialized = false; // For preventing items initializing certain data too early

    super.reset();
  }

  /** @override */
  _initialize(options) {
    super._initialize(options);
    this._initialized = true;
  }

  /**
   * Get item by its identifier tag.
   *
   * @param {string} tag - Desired tag.
   * @returns {Item|undefined} - Matching item or undefined if no item is found.
   */
  getItemByTag(tag) {
    return this.items.find((o) => o.system.tag === tag);
  }

  /**
   * Cached result of .itemTypes
   *
   * @internal
   * @type {ItemTypesMap}
   */
  _itemTypes;

  /**
   * Cached override
   *
   * @override
   * @type {ItemTypesMap}
   */
  get itemTypes() {
    // Return cache
    if (this._itemTypes) return this._itemTypes;

    // Generate new copy (or fetch Foundry cached copy in v13+)
    this._itemTypes = super.itemTypes;

    // HACK: Avoids errors in v13 due to Foundry having its own cache
    if (this._itemTypes.base.getName) return this._itemTypes;

    // Enrich the arrays with getName() and getId()
    for (const items of Object.values(this._itemTypes)) {
      Object.defineProperties(items, {
        getName: {
          value: function (name) {
            return this.find((i) => i.name === name);
          },
        },
        getId: {
          value: function (identifier) {
            return this.find((i) => i.system.tag === identifier);
          },
        },
      });
    }

    return this._itemTypes;
  }

  /**
   * Returns first active owner, favoring players and GM as fallback.
   *
   * @type {User|null}
   */
  get activeOwner() {
    const firstOwner =
      game.users
        .filter((u) => u.active && !u.isGM)
        .filter((u) => this.testUserPermission(u, "OWNER"))
        .sort((a, b) => (a.id > b.id ? 1 : -1))[0] ?? null;

    return firstOwner ?? game.users.activeGM;
  }

  /**
   * Get related combatants.
   *
   * @param {Combat} [combat=game.combat] Combat instance
   * @returns {Combatant[]} Related combatants.
   */
  getCombatants(combat = game.combat) {
    return combat?.combatants.filter((c) => c.actor === this) ?? [];
  }

  /**
   * Whether current user can see through this actor.
   *
   * @type {boolean}
   */
  get sharesVision() {
    const visionFlag = this.getFlag("pf1", "visionSharing");
    if (!visionFlag) return false;

    return visionFlag.users?.[game.user.id] ?? visionFlag.default ?? false;
  }

  /**
   * Temporary solution until Foundry v12 where visibility of AE no longer controls if it's temporary.
   *
   * @internal
   * @returns {ActiveEffectPF[]}
   */
  get _effectsWithDuration() {
    const effects = [];
    for (const effect of this.allApplicableEffects()) {
      if (effect.active && effect._hasDuration) effects.push(effect);
    }
    return effects;
  }

  /**
   * Override to support toggling buffs and operating the system's condition tracks
   *
   * Called by {@link pf1.canvas.TokenHUDPF TokenHUD}
   *
   * @todo Add separate listener for buffs
   * @override
   * @param {string} statusId - Status/condition ID
   * @param {object} [options] - Additional options
   * @param {boolean} [options.active] - Force active state
   * @param {boolean} [options.overlay=false] - Overlay effect
   * @param {boolean} [options.interaction=true] - Nonstandard. Is this triggered by user interaction?
   * @returns {Promise<ActiveEffect|boolean|undefined>}
   *
   * Synced with Foundry v12.331 & v13.342
   */
  async toggleStatusEffect(statusId, { active, overlay = false, interaction } = {}) {
    // Copy of Foundry identification of static ID
    const status = CONFIG.statusEffects.find((e) => e.id === statusId);
    if (status?._id) return super.toggleStatusEffect(statusId, { active, overlay });

    // Support toggling buffs via token HUD
    const [_, buffId] = statusId?.split("buff-") ?? [];
    if (buffId) {
      const buff = this.items.get(buffId);
      if (!buff) throw new Error(`Buff not found to toggle: ${buffId}`);
      const isActive = buff.activeState;
      const state = active ?? !isActive;
      if (state === isActive) return;

      await buff.setActive(state);
      // Slight deviation from API, we do not return created AE here (we don't know if one was created or re-used).
      return state;
    }

    // TODO: Check if this is primary condition, deny modification if not

    // TODO: When disabling, consider if the condition is granted by something else and thus don't touch it here?
    const condition = pf1.registry.conditions.get(statusId);
    // TODO: Move condition handling API from ActorPF to ActorBasePF
    if (condition && this.setCondition) {
      let aeData = active;
      const isActive = this.statuses.has(statusId); // Get current state
      if (overlay && !isActive) aeData = { flags: { core: { overlay: true } } };
      if (aeData !== undefined) return this.setCondition(statusId, aeData);
      return this.toggleCondition(statusId);
    }

    return super.toggleStatusEffect(statusId, { active, overlay });
  }

  /**
   * Condition immunities
   *
   * @internal
   * @returns {Set<string>} - Set of immunity IDs.
   */
  getConditionImmunities() {
    const ci = this.system.traits?.ci?.total ?? [];
    const list = new Set([...ci]);

    // Map immunities to actual conditions
    // TODO: Unify the IDs where possible
    const condToImmMap = {
      confuse: ["confused"],
      daze: ["dazed"],
      dazzle: ["dazzled"],
      fatigue: ["fatigued"],
      fear: pf1.registry.conditions.conditionsInTrack("fear"),
      sicken: ["sickened"],
      paralyze: ["paralyzed"],
      petrify: ["petrified"],
      stun: ["stunned"],
    };
    for (const [key, conditions] of Object.entries(condToImmMap)) {
      if (list.has(key)) {
        for (const cond of conditions) list.add(cond);
      }
    }

    return list;
  }

  /**
   * Per-actor type initiative options
   *
   * @abstract
   * @returns {object} - Options object
   */
  getInitiativeOptions() {
    // TODO: Pull these from system model if possible
    return {};
  }

  /**
   * @abstract
   */
  expireActiveEffects() {}

  /**
   * Movement details for current movement
   *
   * @abstract
   * @param {MovementType} type
   * @returns {MovementInfo|undefined}
   */

  getMovement(type) {
    return this.system.getMovement?.(type);
  }
}

/**
 * All items sorted by type.
 *
 * Also includes any module introduced types.
 *
 * @typedef ItemTypesMap
 * @property {ItemAttackPF[]} attack - Attacks
 * @property {ItemBuffPF[]} buff - Buffs
 * @property {ItemClassPF[]} class - Classes
 * @property {ItemConsumablePF[]} consumable - Consumables
 * @property {ItemContainerPF[]} container - Containers
 * @property {ItemEquipmentPF[]} equipment - Equipment
 * @property {ItemImplantPF[]} implant - Implants
 * @property {ItemFeatPF[]} feat - Features
 * @property {ItemLootPF[]} loot - Loot
 * @property {ItemRacePF[]} race - Race
 * @property {ItemSpellPF[]} spell - Spells
 * @property {ItemWeaponPF[]} weapon - Weapons
 */
